import React from 'react'
// Styles, Assets
import s from './CreateSteps.sass'
// Constants
import { STEPS } from '../../pages/create'

const CreateSteps = ({ step }) => (
  <div className={s.container}>
    <div className={s.line} />
    <div className={s.step}>
      <h2>1</h2>
      <h5 data-active={step === STEPS.REWARD_SETUP}>SETUP REWARD</h5>
    </div>
    <div className={s.step}>
      <h2>2</h2>
      <h5 data-active={step === STEPS.REQUIREMENTS_SETUP}>SETUP REQUIREMENTS</h5>
    </div>
    <div className={s.step}>
      <h2>3</h2>
      <h5 data-active={step === STEPS.DEPLOYMENT}>DEPLOYMENT</h5>
    </div>
  </div>
)

export default CreateSteps